import React, { useEffect, useState } from "react";
import Layout from "../containers/layout";
import { Anchor, Accordion, AccordionPanel, Box, Text } from "grommet";
import { fetchProducts, fetchStores } from "../helpers";
import CardListing from "../components/CardList";

const Home = ({
  location,
  pathContext,
  pageContext,
  storeName,
  id,
  ...props
}) => {
  const [products, setProducts] = useState([]);
  const [store, setStore] = useState(null)
  useEffect(() => {
    fetchStores().then(e => setStore(e.result.items.find(e => `${e.id}` === id)))
    fetchProducts(id).then((e) => setProducts(e.result.items));
  }, []);
  return (
    <>
      <Accordion>
        <AccordionPanel label={storeName}>
          <Box pad="medium" background="light-2">
            <Text dangerouslySetInnerHTML={{ __html: store && store.description}}>{
              
            }</Text>
          </Box>
        </AccordionPanel>
      </Accordion>
      <CardListing
        dataList={products.map((e) => ({
          title: e.name,
          thumbnail: e.thumbnailUrl,
          cta: <>
            <Anchor href={`/store/${e.id}`}>Bid $8</Anchor>
            <Anchor href={`/store/${e.id}`}>Instant Close $8</Anchor>
          </>,
        }))}
      />
      <div id="my-store-41402144"></div>
    </>
  );
};

export default Home;
